function App() {
  const [renderable, setRenderable] = useState(false);
  const [story, setStory] = useState(null);
  const [children, setChildren] = useState([]);
  const [childrenById, setChildrenById] = useState({});
  const [loading, setLoading] = useState(false)
  
  useEffect( () => {
    async function fetchData() {
      let result = await retrieveStory();
      setStory(result);
    }
    fetchData();
	}, [])

  useEffect(() => {
    if(story && story.kids && story.kids.length > 0) {
      getChildItems(story).then((childItems) => {
        let curChildrenById = {}
        childItems.forEach((child) => {
          curChildrenById[child.id] = child;
        });
        setChildren(childItems);
        setChildrenById(curChildrenById);
      })
    }
  }, [story]);

  useEffect(() => {
    if(Array.isArray(children) && children.length > 0)
      setRenderable(true)
    else
      console.log(children)
  }, [children])

  const handleSearch = (event) => {
    // Perform search request
    event.preventDefault();
    setLoading(true);
    console.log('Searching for:', event.target.search.value);
    var bodyFormData = new FormData();
    bodyFormData.append('question', event.target.search.value);

    let response = axios.post(`${apiUrl}/ask`, bodyFormData).then(response => {
      console.log(`children: ${JSON.stringify(response,0,4)}`);
      let newChildren = response.data.searchResults.map((listingId) => {
        //update the children with the response comment ID
        return childrenById[listingId];
      })
      setChildren(newChildren); 
      setLoading(false);    
    });
  }

    return (
      <Router>
        <ThemeProvider theme={theme}>
          <AppBar position="static">
            <Toolbar sx={{textAlign: "center", justifyContent: "center"}}>
              <Button color="inherit" component={Link} to="/demo">
                Demo
              </Button>
              <Button color="inherit" component={Link} to="/">
                Implementation Blog
              </Button>
            </Toolbar>
          </AppBar>
          <Container maxWidth="md">
            <Routes>
              <Route exact path="/" element = {<Blog/>}/>
              {renderable ? (
                <Route exact path="/demo" element = {
                  <Demo
                    loading = {loading}
                    handleSearch={handleSearch}
                    renderable={renderable}
                    story={story}
                    children={children}
                  />
                }/>
              ) : ( 
                <Route exact path="/demo" element = {
                  <DNA
                    visible={true}
                    height="80"
                    width="80"
                    ariaLabel="dna-loading"
                    wrapperStyle={{ position: "fixed", top: "50%", left: "50%", transform: "translate(-50%, -50%)" }}
                    wrapperClass="dna-wrapper"
                  />
                }/>
              )}
            </Routes>
          </Container>
        </ThemeProvider>
      </Router>
    );

}

function Demo( props ) {
  const { handleSearch, loading, renderable, story, children } = props;
  return (
  <Container maxWidth="lg">
    <Typography variant="h2" gutterBottom>
      Who's Hiring AI Search
    </Typography>
    <form onSubmit={handleSearch} sx={{ m: 1, flexGrow: 1 }}>     
      <Stack direction="row" alignItems="center" spacing={2}>
        <TextField
          id="search"
          name="search"
          label="Search"
          variant="outlined"
          size="small"
          fullWidth
        />            
          <div>
            { 
              loading ? 
                <DNA
                  visible={true}
                  height="80"
                  width="80"
                  ariaLabel="dna-loading"
                  wrapperStyle={{ }}
                  wrapperClass="dna-wrapper"
                /> : 
                <Button type="submit" variant="contained">
                  Search
                </Button>
            }
          </div>
      </Stack>
    </form>
    {renderable ? (
      <List>
        <>
        <ListItem>
          <ListItemText primary={story.title} secondary={`by ${story.by}`} />
        </ListItem>
        <List>
          {
            children.map((child) => (
              <ListItem key={child.id}>
                <ListItemText primary={`${child.text}`} secondary={`by ${child.by}`} />
              </ListItem>
            ))
          }
        </List>
        </>
      </List>
      ) : (
      <Typography>Loading...</Typography>
    )}
  </Container>)
}

function Blog() {
  const runHandler = `def poll_for_results(client, run, listings):
  while run.status in ['queued', 'in_progress']:
    time.sleep(0.05)
    run = client.beta.threads.runs.retrieve(
      thread_id=run.thread_id,
      run_id=run.id
    )
  if run.status == "requires_action":
    ## handle tool calls here
  elif run.status == "failed":
    ## handle failures
  else:
    ## we have our result!
    message_list = client.beta.threads.messages.list(
      thread_id=run.thread_id,
      order="asc"
    )
  `
  const templateString = `template = """
  Given the following extracted parts of a hacker news story ("SOURCES") and a question ("QUESTION"), create a final answer.
  Don't try to make up an answer and use the text in the SOURCES only for the answer. If you don't know the answer, just say that you don't know.
  Provide your answer in the following json format. There must be one source per entry and you cannot use 'all sources'.
  {{"docs": [ 
      {{
          "source": source1
    }}
  ]}}
  =========
  QUESTION: {question}
  =========
  SOURCES:
  {summaries}
  =========
  ANSWER:
  """
  `
  return (
    <Container maxWidth="lg">
      <Typography variant="h2" gutterBottom>
        Searching Whos Hiring with Natural Language 
      </Typography>
      <Typography variant="h3" gutterBottom> 
        Querying monthly HackerNews job postings using OpenAI Assistants
      </Typography>
      <Typography variant="body1" gutterBottom>
        <p>
          I met someone at a potluck who will be finishing her masters degree in the next few months. She was having trouble finding jobs. I asked some of the usual questions: "what do you enjoy doing?", "what kind of roles are you targeting?", "what was your job experience before school?". I made some on the spot recommendations, but then went home and decided to look over HackerNews' "Who's Hiring?"
        </p>
        <p>
          What I love about "Who's Hiring?" is that it's usually a list of new job postings that can get you a referral. But the second you get out of looking for engineering jobs, you have your first filter. Let's say we're looking for Dev Ops roles. Do I search for Dev Ops? What about Infrastructure Engineer? Cloud Engineer? It's quite easy to Ctrl+F for the wrong word and miss job postings. When you want to add another constraint, like a city or an industry, it just becomes impossible to do well from the browser.
        </p>
        <p>
          What I wanted was a way for someone to search Who's Hiring in a natural way, like talking to a friend. RAG, or Retrieval Augmented Generation, is an approach that lets you use external information in combination with Generative AI to search through products with natural language. While I'd done a similar PoC with vector databases, I wanted to use a number of recently released OpenAI features to see how they performed.
        </p>
        <p>
          At Dev Day, OpenAI released the gpt-41106-preview model which brings a number of new features wrapped inside of 'Assistants'. Assistants allow you to use built in storage, run tools like your custom functions or ChatGPT managed Code Interpreter (in parallel!), and persist conversations in individual threads. In this case, that means that we can create an assistant to answer natural language requests about job listings that uses the 'Who's Hiring' thread as a data source and create a function that enforces we get the results back in our expected format.
        </p>
        <VideoPlayer src='AssistantsDemo720p.mov' />
      </Typography>
      <Typography variant="h3">OpenAI Assistants Application</Typography>
      <Typography variant="body1">
        <p>
          Conceptually, the pieces to build our Assistant are very simple. There are three parts  a HN scraper which returns all of the comments for a particular HN story, the Assistant builder which takes those in a file format, and a function definition that defines the expected format of a response.
        </p>
        <img src='AssistantsFlow.png' width='100%'></img>
      </Typography>
      <Typography variant="h3">Search Handling API</Typography>
      <Typography variant="body1">
        <p>
          After we build our assistant, we need an API boundary that our web application can call out to to handle forwarding those to our ChatGPT assistant. This lets us keep our OpenAI API Key on the API server. As a simple PoC, I created a Flask App, but in practice this is more likely to be an API Gateway that can handle authentication, authorization, and usage tracking for the requests.
        </p>
        <p>
          The Search Handling API needs to work around the Assistants threading model. As part of a run against an assistant, you are returned a thread which handles the run step-by-step. When we receive an event that isnt just ChatGPT doing its thing, we need to build in handling. This is given to us by a status of required_action. Practically, when we receive a call to our JSON formatter, were responsible for submitting the result of the tool back to ChatGPT.
        </p>
        <SyntaxHighlighter children={runHandler} language="python" style={dark}></SyntaxHighlighter>
        <p>
          We then return the results of the call back to the web app in the structured format weve directed ChatGPT to generate.
        </p>
      </Typography>
      <Typography variant="h3">React Application</Typography>
      <Typography variant="body1">
        <p>Finally, we have a simple React App which handles visualizing the results.</p>
        <i>Viewing Job Listings</i>
        <p>We also scrape the post here to render an initial page of job listings. In the React App, we map comment ids to their job listing content. We tell React to rerender the page when the listings we want to render change. </p>
        <i>Searching Job Listings</i>
        <p>We have a search box that sends requests to our Marqo app. When we receive a response, we set the children to the list of sources returned by our endpoint which changes our view.</p>
      </Typography>
      <Typography variant="h3">Vector Database Approach</Typography>
      <Typography variant="body1"> 
        <p>
          The Vector Database Approach differs in a few ways. Rather than just write a flat-file back to OpenAI assistants, we generate the encodings for each of the comments using a preferred model. The upfront indexing means that we have a performant vector database to work against and get back initial batches of results based on a natural language input.
        </p>
        <p>
          These initial batches of results are often quite good, but they judge the similarity based on the proximity (defined by common vector distance approaches) in the databases, and often results can be similar like a fork and a spoon are similar when youre eating soup. ChatGPT can then be used as a sanity check against the most similar results returned by the VectorDB.
        </p>
      </Typography>
      <p></p>
      <VideoPlayer src="Demo.mov" />
      <p></p>
      <Typography variant="h3">Comparison</Typography>
      <Typography variant="body1">
        <p>
          While the API is very conceptually clean and getting the PoC up-and-running was quick, the performance of the API has been more than ten times as slow as using a combination of Vector Databases and ChatGPT. I attribute a lot of this to the embeddings. When using a Vector DB, I was able to get similar results with much lower dimensional embeddings. As eventually the requests will go through ChatGPT, we can offload a lot of the computational overload to later in the process. 
        </p>
        <p>
          This might be due to the fact that OpenAI Assistants decides whether to index files using a Vector Database or to pass the entire file into the context window. From the documentation:
        </p>
      </Typography>
      <Typography variant="body1">
        <blockquote>
          <i>
            The model then decides when to retrieve content based on the user Messages. The Assistants API automatically chooses between two retrieval techniques: it either passes the file content in the prompt for short documents, or performs a vector search for longer documents. Retrieval currently optimizes for quality by adding all relevant content to the context of model calls. We plan to introduce other retrieval strategies to enable developers to choose a different tradeoff between retrieval quality and model usage cost.
          </i>
        </blockquote>
      </Typography>
      <Typography variant="body1">
        <p>
          The length of Who's Hiring, including JSON formatting breaking out comment IDs and the message text, is typically in the 400,000-500,000 range. Both of these converted to tokens (approximated by dividing by 4) are typically going to be less than the 128,000 token context window used by the GPT4 version that supports assistants. This explains both the slow processing and the high token consumption. By adding filler comments to get over the 128,000 token length, performance was improved, but still worse than the dedicated vector database approach.
        </p>
        <p>
          With Vector Databases, you also have a good idea of when your queries are breaching context window thresholds. For instance, if I search for Software Engineer Jobs then Im likely going to get hundreds of hits that are going to exceed the context window. That becomes opaque in the Assistants approach, and you lose the ability to go back to a customer and say please provide more detail.
        </p>
        <p>
          Another implication here is cost. Running a few dozen of these queries without adding padding to enforce vectorization puts me at the daily usage threshold for the beta gpt4 model. With the Vector DB approach, you can ensure that you're only bringing in the relevant context for scanning. For any application doing something similar over a small data set where the Assistant has not decided to index your file into a Vector DB, this can cause you to consume more tokens than you might expect.
        </p>
        <p>
          The largest quality difference here is when you're searching for queries that may have many semantically similar matches based on common words in the search phrase. For instance, "Software engineering jobs using AWS" contains very few words that are likely to be unique in the search space. The better embeddings with the GPT4 beta gave much more relevant results. However, if we were to calculate the embeddings for the vector DB using the GPT4 embeddings, I imagine that the quality difference here would narrow.
        </p>
        <p>
          Overall, Assistants provide a great interface for either highly complex chains of functions that you need to run or an experience that you need to launch quickly, but wouldnt be my choice if I were building a user-facing app that was latency sensitive and depended on the entire result being present.
        </p>
      </Typography>
  </Container>
  )
}

